import React from "react";
import { Button, Container, Grid, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import NewsCard from "../../components/NewsCard";
import { useNavigate } from "react-router";
import { useSelector } from "react-redux";

const News = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const { news } = useSelector(({ site }) => site);

  return (
    <section className={classes.section}>
      <Container
        maxWidth="xl"
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Typography variant="h2">Новости и акции</Typography>
        <Typography
          variant="h6"
          sx={{
            maxWidth: 573,
            margin: { xs: "15px 0 30px", md: "7px 0 40px" },
            fontWeight: "normal",
            textAlign: "center",
          }}
        >
          Будьте в курсе лучших предложений от нашей компании.
        </Typography>
        <Grid container spacing={4}>
          {Array.isArray(news) &&
            news
              .reverse()
              .slice(0, 3)
              .map((item, idx) => (
                <Grid item xs={12} sm={6} md={4} key={idx}>
                  <NewsCard item={item} idx={idx} main />
                </Grid>
              ))}
        </Grid>
        <Button
          aria-label="read more news"
          size="medium"
          color="primary"
          onClick={() => navigate("/news")}
          variant="contained"
          sx={{ mt: { xs: 3, md: 5 } }}
        >
          Другие новости
        </Button>
      </Container>
    </section>
  );
};

export default News;

const useStyles = makeStyles((theme) => ({
  section: {
    background: "#F5F5F7",
    padding: "80px 0",
    "& img": {
      width: "100%",
    },

    [theme.breakpoints.down("768")]: {
      padding: "50px 0",
    },
  },
}));
