import { Box, Button } from "@mui/material";
import { useFormik } from "formik";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import { DirectionForm } from "../../../components/Forms";
import SelectCustom from "../../../components/Select";
import {
  getEnvelopesByFilter,
  setOrderingData,
} from "../../../redux/reducers/ordering";

const Calculator = ({ tab }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);

  const formik = useFormik({
    initialValues: {
      from: "",
      to: "",
      width: "",
      height: "",
      weight: "",
      length: "",
      envelop: "",
    },
    onSubmit: (values) => {
      navigate("/calculator");
      dispatch(setOrderingData(values));
    },
  });

  useEffect(() => {
    if (formik.values.from && formik.values.to)
      dispatch(getEnvelopesByFilter(formik.values.from, formik.values.to));
  }, [formik.values.to]);

  return (
    <Box
      component="form"
      onSubmit={formik.handleSubmit}
      sx={{
        padding: { xs: "20px 20px", lg: "35px 46px 50px" },
        background: "#F5F5F7",
        boxShadow: "0px 0px 12px rgba(0, 0, 0, 0.25)",
      }}
    >
      <Box
        display="flex"
        mt={4}
        alignItems="start"
        sx={{ flexDirection: { xs: "column", lg: "row" } }}
      >
        <DirectionForm formik={formik} />

        <SelectCustom
          formik={formik}
          open={open}
          setOpen={setOpen}
          label="Размер"
          main
        />

        <Button
          aria-label="submit form"
          type="submit"
          sx={{
            ml: { xs: 0, lg: 3 },
            mt: { xs: 3, lg: 0 },
          }}
          variant="contained"
          color="primary"
          size="large"
        >
          {
            // eslint-disable-next-line
            tab == 1 ? "Вызвать" : "рассчитать"
          }
        </Button>
      </Box>
    </Box>
  );
};

export default Calculator;
