import { Container } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/pagination";

import SwiperCore, { Pagination, Autoplay } from "swiper";
import { useDispatch, useSelector } from "react-redux";
import { getNewsData } from "../../redux/reducers/site";
import { Link } from "react-router-dom";

SwiperCore.use([Pagination, Autoplay]);

const Slides = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { news } = useSelector(({ site }) => site);

  useEffect(() => {
    if (!news[0]) {
      dispatch(getNewsData());
    }
  }, []);

  return (
    <section className={classes.section}>
      <Container maxWidth="xl">
        <Swiper
          slidesPerView={2}
          spaceBetween={30}
          pagination={{
            clickable: true,
          }}
          className={classes.swiper}
          autoplay={{
            delay: 3000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
          }}
          breakpoints={{
            0: {
              slidesPerView: 1,
              slidesPerGroup: 1,
              spaceBetween: 0,
            },

            768: {
              slidesPerView: 1,
              slidesPerGroup: 1,
              spaceBetween: 0,
            },

            992: {
              slidesPerView: 2,
              slidesPerGroup: 2,
              spaceBetween: 30,
            },
          }}
        >
          {Array.isArray(news) &&
            news
              ?.filter((item) => item.category === "Акции")
              .map((item, idx) => (
                <SwiperSlide style={{ position: "relative" }} key={idx}>
                  <Link
                    to={`/news/${item.id}`}
                    style={{
                      display: "block",
                      height: "100%",
                      width: "100%",
                    }}
                  >
                    <img
                      src={item.border_photo}
                      style={{ width: "100%" }}
                      alt=""
                    />
                  </Link>
                </SwiperSlide>
              ))}
        </Swiper>
      </Container>
    </section>
  );
};

export default Slides;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "20px 0 60px",
    background: "#F5F5F7",
    position: "relative",
  },

  box: {
    position: "absolute",
    top: 51,
    left: 51,
    right: 51,

    [theme.breakpoints.down("768")]: {
      top: "30px",
      left: "30px",
      right: "30px",
    },
  },

  swiper: {
    position: "static!important",

    "& .swiper-pagination-bullets": {
      bottom: "20px",
      "& .swiper-pagination-bullet": {
        width: 16,
        height: 16,
        background: "#FFB951",
        opacity: 1,
        [theme.breakpoints.down("768")]: {
          width: 12,
          height: 12,
        },
      },

      "& .swiper-pagination-bullet-active": {
        background: "#1C1C1C",
        border: "2px solid var(--color-primary)",
      },
    },
  },
}));
