const FirstScreen = () => {
  const classes = useStyles();
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down("992"));
  const dispatch = useDispatch();

  const { news } = useSelector(({ site }) => site);

  return (
    <section className={classes.section}>
      <Container maxWidth="xl">
        <Box
          sx={{
            display: "flex",
            overflowX: "auto",
            pt: 2,
            flexWrap: "nowrap",
          }}
        >
          {Array.isArray(news) &&
            news
              .filter((item) => item.category === "Полезное")
              ?.map((item, idx) => <Messages item={item} key={idx} />)}
        </Box>
        <Grid container className={classes.grid}>
          <Grid item xs={12} lg={6}>
            <Typography variant="h1" className={classes.title}>
              DOCExpress - быстро и надежно доставим все что можно.
            </Typography>
            <Typography
              variant="h6"
              lineHeight="1.2"
              sx={{
                margin: { xs: "20px 0 25px", lg: "30px 0 49px" },
              }}
            >
              Бишкек, Ош, Джалал-Абад, Баткен, Нарын, Иссык-Куль, Талас, Чуй.
            </Typography>
            <Box display="flex">
              <Box
                display="flex"
                alignItems="center"
                sx={{ mr: { xs: 1, lg: 0 } }}
              >
                <CheckMarkIcon />
                <Typography
                  variant="h6"
                  component="span"
                  sx={{ marginLeft: "20px" }}
                >
                  Низкая стоимость и быстрая доставка
                </Typography>
              </Box>
              <Box display="flex" alignItems="center">
                <CheckMarkIcon />

                <Typography
                  variant="h6"
                  component="span"
                  sx={{ marginLeft: "20px" }}
                >
                  Бесплатное и удобное отслеживание
                </Typography>
              </Box>
            </Box>
            <Button
              size="large"
              variant="contained"
              color="primary"
              sx={{ marginTop: { xs: "30px", lg: "49px" } }}
              onClick={() => dispatch(setFeedbackDialog(true))}
            >
              Оставить заявку
            </Button>
          </Grid>
          {lg && (
            <Grid item xs={12}>
              <img
                src={man}
                width="100%"
                height="100%"
                className={classes.img}
                alt=""
              />
            </Grid>
          )}
        </Grid>
      </Container>
    </section>
  );
};

export default FirstScreen;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "0 0 132px",
    background: `no-repeat url('${bg}') center/cover`,

    [theme.breakpoints.down(992)]: {
      background: `no-repeat url('${delivery}') left/cover`,
      padding: 0,
    },
  },

  title: {
    maxWidth: 574,
  },

  grid: {
    paddingTop: 138,

    [theme.breakpoints.down(992)]: {
      paddingTop: 80,
    },
  },
}));

