import { Button, Grid, Typography } from "@mui/material";
import React from "react";
import { Link } from "react-router-dom";

const NewsCard = ({ item, idx, main }) => {
  return (
    <Link to={`/news/${item.id}`}>
      <img
        src={item.border_photo}
        alt={item.title}
        style={{ marginBottom: 20, borderRadius: 6, height: "216px", objectFit: "cover" }}
      />
      <Typography variant="h6" mb={1} className="mb-13">
        {item.title}
      </Typography>
      <Typography
        variant="body2"
        fontWeight={300}
        lineHeight="1.5"
        className="mb-20"
        dangerouslySetInnerHTML={{
          __html: item.description.substring(0, 80) + "...",
        }}
      />

      {!main && (
        <Grid container alignItems="center" justifyContent="space-between">
          <Button variant="btnPrimaryOval">{item.category}</Button>
          <Typography variant="body2" fontWeight={600}>
            {item.date}
          </Typography>
        </Grid>
      )}
    </Link>
  );
};

export default NewsCard;
