import {
  Box,
  ClickAwayListener,
  FormControlLabel,
  Radio,
  RadioGroup,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { LittleTab, LittleTabs } from "../../shared/LittleTabs";

const SelectCustom = ({ formik, open, setOpen, label, ordering, main }) => {
  const classes = useStyles();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down("md"));

  const handleClick = () => {
    setOpen((prev) => !prev);
  };

  const handleClickAway = () => {
    setOpen(false);
  };

  const [value, setValue] = useState(0);

  const { envelops_prices } = useSelector(({ ordering }) => ordering);

  const handleChangee = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(
    () => {
      // eslint-disable-next-line
      if (value == 1) {
        formik.setFieldValue(
          "envelop_label",
          `${formik.values.length}x${formik.values.width}x${formik.values.height} см, до ${formik.values.weight} кг `
        );
      }
    },
    // eslint-disable-next-line
    [
      formik.values.width,
      formik.values.height,
      formik.values.length,
      formik.values.weight,
    ]
  );

  return (
    <ClickAwayListener onClickAway={handleClickAway}>
      <Box
        className={open ? `${classes.select} Mui-expanded ` : classes.select}
        sx={
          !ordering
            ? {
                maxWidth: { xs: "100%", lg: 240 },
                minWidth: { xs: "100%", lg: 240 },
                ml: { xs: 0, lg: 3 },
                mt: { xs: 3, lg: 0 },
              }
            : {}
        }
      >
        <input
          value={formik.values.envelop}
          required={
            !formik.values.weight ||
            !formik.values.height ||
            !formik.values.length ||
            !formik.values.width
              ? true
              : false
          }
          onChange={formik.handleChange}
          style={{
            opacity: 0,
            position: "absolute",
            userSelect: "none",
            width: "100%",
            zIndex: "-1",
            height: "100%",
          }}
        />
        <Box
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          width="100%"
          height="100%"
          padding="0 16px"
        >
          <Typography
            variant="body2"
            sx={{
              display: "flex",
              alignItems: "center",
              width: "100%",
              height: "100%",
            }}
            onClick={handleClick}
          >
            {formik.values.envelop_label
              ? formik.values.envelop_label
              : formik.values.envelop
              ? formik.values.envelop
              : label}
          </Typography>
          <i
            className="ri-arrow-down-s-line"
            onClick={handleClick}
            style={{
              fontSize: "24px",
              display: "inline-block",
            }}
          />
        </Box>
        <div
          className={
            open ? `${classes.option} ${classes.option_open}` : classes.option
          }
        >
          {ordering && md ? (
            <LittleTab value={value} onChange={handleChangee}>
              <LittleTabs label="Примерно" />
              <LittleTabs label="Точный размер" />
            </LittleTab>
          ) : !ordering ? (
            <LittleTab value={value} onChange={handleChangee}>
              <LittleTabs label="Примерно" />
              <LittleTabs label="Точный размер" />
            </LittleTab>
          ) : (
            ""
          )}
          {
            // eslint-disable-next-line
            value == 1 ? (
              <>
                <TextField
                  type="number"
                  step="0.000000001"
                  required={!formik.values.envelop}
                  className={classes.TextField}
                  fullWidth
                  name="length"
                  value={formik.values.length}
                  onChange={formik.handleChange}
                  label="Длина, см"
                />
                <TextField
                  type="number"
                  step="0.000000001"
                  required={!formik.values.envelop}
                  className={classes.TextField}
                  fullWidth
                  name="width"
                  value={formik.values.width}
                  onChange={formik.handleChange}
                  label="Ширина, см"
                />
                <TextField
                  type="number"
                  step="0.000000001"
                  required={!formik.values.envelop}
                  className={classes.TextField}
                  fullWidth
                  name="height"
                  value={formik.values.height}
                  onChange={formik.handleChange}
                  label="Высота, см"
                />
                <TextField
                  type="number"
                  step="0.000000001"
                  required={!formik.values.envelop}
                  className={classes.TextField}
                  fullWidth
                  name="weight"
                  value={formik.values.weight}
                  onChange={formik.handleChange}
                  label="Вес, кг"
                />
              </>
            ) : !formik.values.to ? (
              <>
                <Typography variant="body1" mb={2}>
                  Выберите направление
                </Typography>
              </>
            ) : (
              <>
                {Array.isArray(envelops_prices) ? (
                  envelops_prices?.map((item) => (
                    <RadioGroup
                      value={formik.values.envelop}
                      name="envelop"
                      key={item.envelop}
                    >
                      <Box
                        onClick={() => setOpen(false)}
                        display="flex"
                        alignItems="flex-start"
                        justifyContent="space-between"
                        width="100%"
                        sx={{
                          borderBottom: "1px solid #F4F4F4",
                          mb: 2,
                          pb: 2,
                        }}
                        component="label"
                      >
                        <div>
                          <Typography
                            variant="body2"
                            mb={1}
                            sx={{ fontWeight: 600 }}
                          >
                            {item.title}
                          </Typography>
                          <Typography
                            variant="body2"
                            sx={{
                              fontWeight: 400,
                              maxWidth: 230,
                              whiteSpace: "pre-wrap",
                            }}
                          >
                            {item.description}
                          </Typography>
                        </div>
                        <FormControlLabel
                          value={item.title}
                          control={<Radio />}
                          onChange={formik.handleChange}
                          label=""
                        />
                      </Box>
                    </RadioGroup>
                  ))
                ) : (
                  <Typography variant="subtitle1" mb={2}>
                    К сожалению по данному направлению нет тарифов
                  </Typography>
                )}
              </>
            )
          }
        </div>
      </Box>
    </ClickAwayListener>
  );
};

export default SelectCustom;

const useStyles = makeStyles(() => ({
  select: {
    background: "#EDEDEF",
    borderRadius: 8,
    position: "relative",
    height: 60,
    zIndex: 1,
    transition: "all 0.1s linear",
    border: "1px solid transparent",
    userSelect: "none",

    "&:hover": {
      border: "1px solid #000",
    },

    "&.Mui-expanded": {
      borderRadius: "8px 8px 0 0",
    },
  },

  option: {
    transition: "all 0.2s ease",
    transform: "scale(0.5)",
    transfromOrigin: "bottom left",
    background: "#EDEDEF",
    opacity: 0,
    borderRadius: "0 0 8px 8px",
    padding: "16px 20px 0",
    maxHeight: 350,
    overflowY: "scroll",
    borderTop: "1px solid #D3D3D3",
    zIndex: 10,
    marginTop: 1,
  },

  option_open: {
    opacity: 1,
    width: "100%",

    background: "#EDEDEF",
    transform: "scale(1)",
  },

  TextField: {
    marginBottom: "10px!important",
    display: "block",
    "& .MuiOutlinedInput-root": {
      background: "#FFF!important",
      borderRadius: "8px",
    },
  },
}));
