import { Container, Tab, Tabs } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { useState } from "react";
import Calculator from "./Calculator";
import Tracking from "./Tracking";

const Forms = ({ setLoadingParcel }) => {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <section className={classes.section}>
      <Container maxWidth="xl">
        <Tabs value={value} onChange={handleChange} className={classes.tabs}>
          <Tab label="Калькулятор" className={classes.tab} />
          <Tab label="Вызвать курьера" className={classes.tab} />
          <Tab label="Отследить" className={classes.tab} />
        </Tabs>
        {value === 0 ? (
          <Calculator tab={value} />
        ) : value === 1 ? (
          <Calculator tab={value} />
        ) : (
          <Tracking setLoadingParcel={setLoadingParcel} />
        )}
      </Container>
    </section>
  );
};

export default Forms;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "50px 0",
  },

  tabs: {
    "& .MuiTabs-flexContainer": {
      overflow: "auto",
    },
  },
  tab: {
    display: "inline-block",
    padding: "27px 87px!important",
    marginRight: "30px!important",
    borderRadius: "8px 8px 0px 0px!important",
    background: "#EFEFEF!important",

    "&.Mui-selected": {
      background: "#FFB951!important",
      color: "#000!important",
    },

    [theme.breakpoints.down("md")]: {
      padding: "22px 50px!important",
    },
  },
}));
