import {
  FormControl,
  InputAdornment,
  InputLabel,
  ListSubheader,
  MenuItem,
  Select,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import Arrows from "../../pages/Home/Forms/Arrows";
import Magnifier from "../../pages/Home/Forms/Magnifier";
import SelectCustom from "../Select";

export const renderSelectGroup = (item, formik) => {
  const items = item.district_set.map((district) => {
    return (
      <MenuItem
        sx={{
          fontWeight: "normal",
          pl: 4,
        }}
        key={district.id}
        value={district.id}
      >
        {district.name}
      </MenuItem>
    );
  });
  return [
    <ListSubheader
      sx={{
        fontWeight: "bold",
        color: "#000",
      }}
    >
      {item.name}
    </ListSubheader>,
    items,
  ];
};

export const DirectionForm = ({ formik, ordering, toutePanel }) => {
  const classes = useStyles();
  const { regions } = useSelector(({ account }) => account);

  useEffect(() => {
    const regionTo = regions?.data
      ?.map((region) =>
        region.district_set.filter(
          (district) => district.id === formik.values.from
        )
      )
      .filter((item) => Boolean(item.length));
    const regionFrom = regions?.data
      ?.map((region) =>
        region.district_set.filter(
          (district) => district.id === formik.values.to
        )
      )
      .filter((item) => Boolean(item.length));

    formik.setFieldValue(
      "regionTo",
      regionTo && regionTo[0] && regionTo[0][0].region
    );
    formik.setFieldValue(
      "regionFrom",
      regionFrom && regionFrom[0] && regionFrom[0][0].region
    );
  }, [formik.values.from, formik.values.to]);

  // const renderCitiesGroup = (item, to) => {
  //   const items = item.district_set
  //     .slice(0, toutePanel ? 2 : 3)
  //     .map((district) => {
  //       return (
  //         <Typography
  //           key={district.id}
  //           variant="subtitle1"
  //           component="span"
  //           onClick={() =>
  //             formik.setFieldValue(to ? "to" : "from", district.id)
  //           }
  //           className={classes.txtButton}
  //         >
  //           {district.name}
  //         </Typography>
  //       );
  //     });

  //   return [items];
  // };

  return (
    <Box
      position="relative"
      display="flex"
      className="direction"
      sx={{
        flexDirection: { xs: "column", md: "row" },
        width: !ordering
          ? { xs: "100%", md: "fit-content" }
          : {
              xs: "100%",
              lg: "630px",
            },
      }}
    >
      <Box
        sx={{ mr: 4, mb: { xs: 2, md: 0 }, width: { xs: "100%", lg: "50%" } }}
      >
        <FormControl
          fullWidth
          sx={{
            mb: 1,
            minWidth: { xs: "100%", lg: toutePanel ? 200 : 270 },
          }}
        >
          <InputLabel>Откуда</InputLabel>
          <Select
            label="Откуда"
            name="from"
            required
            value={formik.values.from}
            onChange={formik.handleChange}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <Magnifier />
                </InputAdornment>
              ),
            }}
          >
            {Array.isArray(regions?.data) &&
              regions?.data
                ?.filter((item) => item.name !== formik.values.regionFrom)
                .map((item) => renderSelectGroup(item))}
          </Select>
        </FormControl>
        {to.slice(0, 3).map((item) => (
          <Typography
            component="span"
            key={item.id}
            variant="subtitle1"
            onClick={() => formik.setFieldValue("from", item.id)}
            className={classes.txtButton}
          >
            {item.title}
          </Typography>
        ))}
      </Box>

      <Arrows formik={formik} />

      <Box sx={{ width: { xs: "100%", lg: "50%" } }}>
        <FormControl
          fullWidth
          sx={{
            mb: 1,
            minWidth: { xs: "100%", lg: toutePanel ? 200 : 270 },
          }}
        >
          <InputLabel>Куда</InputLabel>
          <Select
            label="Куда"
            name="to"
            required
            value={formik.values.to}
            onChange={formik.handleChange}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <Magnifier />
                </InputAdornment>
              ),
            }}
          >
            {Array.isArray(regions?.data) &&
              regions?.data
                ?.filter((item) => item.name !== formik.values.regionTo)
                .map((item) => renderSelectGroup(item))}
          </Select>
        </FormControl>
        {to.slice(3).map((item) => (
          <Typography
            component="span"
            key={item.id}
            variant="subtitle1"
            onClick={() => formik.setFieldValue("to", item.id)}
            className={classes.txtButton}
          >
            {item.title}
          </Typography>
        ))}
      </Box>
    </Box>
  );
};

const useStyles = makeStyles(() => ({
  txtButton: {
    textDecoration: "underline",
    marginRight: "16px!important",
    cursor: "pointer",

    "&:last-child": {
      marginRight: "0!important",
    },
  },
}));

export const Size = ({ formik, open, setOpen }) => {
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <Box display="flex">
      <Box
        display="flex"
        flexDirection="column"
        sx={{ width: { xs: "100%", md: 370 } }}
      >
        {!md && (
          <Typography variant="body2" mb={2} sx={{ fontWeight: 600 }}>
            Примерно
          </Typography>
        )}
        <SelectCustom
          ordering
          formik={formik}
          open={open}
          setOpen={setOpen}
          label="Размер"
        />
      </Box>
      {!md && (
        <Box
          display="flex"
          flexDirection="column"
          sx={{
            ml: { xs: 0, md: 4 },
          }}
          width="370px"
        >
          <Typography variant="body2" sx={{ fontWeight: 600 }}>
            Точный размер{" "}
            {formik.values.height &&
            formik.values.weight &&
            formik.values.length &&
            formik.values.width ? (
              <>
                :{" "}
                {` Вес (в кг) ${formik.values.weight}, Габариты ${formik.values.width}х${formik.values.length}х${formik.values.height} (в см)`}
              </>
            ) : null}
          </Typography>
          <TextField
            type="number"
            InputProps={{
              inputProps: {
                min: 1,
              },
            }}
            required={!formik.values.envelop}
            fullWidth
            name="width"
            value={formik.values.width}
            onChange={formik.handleChange}
            placeholder="Ширина, см"
            variant="outlined"
            sx={{ mt: 2 }}
          />
          <TextField
            type="number"
            required={!formik.values.envelop}
            fullWidth
            InputProps={{
              inputProps: {
                min: 1,
              },
            }}
            name="length"
            value={formik.values.length}
            onChange={formik.handleChange}
            placeholder="Длина, см"
            variant="outlined"
            sx={{ mt: 2 }}
          />
          <TextField
            type="number"
            InputProps={{
              inputProps: {
                min: 1,
              },
            }}
            required={!formik.values.envelop}
            fullWidth
            name="height"
            value={formik.values.height}
            onChange={formik.handleChange}
            placeholder="Высота, см"
            variant="outlined"
            sx={{ mt: 2 }}
          />
          <TextField
            type="number"
            InputProps={{
              inputProps: {
                min: 1,
              },
            }}
            required={!formik.values.envelop}
            placeholder="Вес, кг"
            name="weight"
            value={formik.values.weight}
            onChange={formik.handleChange}
            variant="outlined"
            sx={{ mt: 2 }}
          />
        </Box>
      )}
    </Box>
  );
};

